import { utils } from '../utils/utils';
import { domUtils } from '../utils/dom-utils';

const { getParent } = domUtils;

export default function isAncestorFixed(element: HTMLElement) {
  if (utils.isWindow(element) || utils.isDocument(element)) {
    return false;
  }
  const doc = utils.getDocument(element);
  const body = doc.body;
  for (
    let parent = getParent(element);
    // 修复元素位于 document.documentElement 下导致崩溃问题
    parent && parent !== body && parent !== doc;
    parent = getParent(parent as HTMLElement)
  ) {
    const positionStyle = utils.css(parent as HTMLElement, 'position');
    if (positionStyle === 'fixed') {
      return true;
    }
  }
  return false;
}
